import React, { Component } from "react";
import "./index.css";
class MobilePage extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  componentDidMount() {
    console.log(this.props.theme, " this.props.theme");
    const cssUrl =
      this.props.theme === "default"
        ? "/assets/css/default.css"
        : "/assets/css/tech.css";
    this.addStyle(cssUrl);
  }

  addStyle = (url) => {
    const style = document.createElement("link");
    style.href = url;
    style.rel = "stylesheet";
    style.async = true;
    document.head.appendChild(style);
  };
  render() {
    const { productInfo } = this.props;
    return (
      <div
        className="mobile-page-container"
        style={{ height: window.screen.height }}
      >
        <img
          src={`/assets/${
            this.props.theme === "default" ? "default" : "tech"
          }.svg`}
          alt=""
          className="mobile-bg"
        />

        <div className="mobile-warning">
          <div
            style={
              this.props.theme === "default"
                ? { color: "rgba(72, 72, 72, 1)" }
                : { color: "white" }
            }
          >
            <div style={{ fontSize: 30, margin: 20, fontWeight: 500 }}>
              {productInfo.productName}
            </div>
            <div style={{ fontSize: 30, margin: 20, fontWeight: 500 }}>
              移动端暂未开发
            </div>
            <div style={{ opacity: 0.8 }}>
              为了更好的用户体验，请使用电脑端访问
            </div>
            <div style={{ opacity: 0.8 }}>给您带来的不便敬请谅解</div>
          </div>
        </div>
      </div>
    );
  }
}

export default MobilePage;
